<template>
  <v-container fluid fill-height style="padding:0;" id="bar" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="barchart">绘制柱状图</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <a target="_blank" href="https://github.com/qzruncode/d3-typescript">代码</a>
                    <div class="chartbox"></div>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import * as d3 from 'd3';

export default {
  components: { Blog },
  data() {
    return {
      time: "2019-08-16",
      title: "绘制柱状图",
      items: [
        {
          title: "柱状图",
          target: "#barchart"
        },
        { divider: true },
      ],
    };
  },
  mounted() {
    const config = {
        chartWidth: 600,
        chartHeight: 300,
        chartMarginL: 50,
        chartMarginR: 50,
        chartMarginT: 50,
        chartMarginB: 50,
    };
    const lineChart = (Config, ChartData, XData) => {
        const x = d3.scaleLinear()
            .domain([0, XData.length])
            .range([0, Config.chartWidth - Config.chartMarginR - Config.chartMarginL]);

        const y = d3.scaleLinear()
            .domain([0, 1.1 * (d3.max(ChartData, (d) => d.value))])
            .range([Config.chartHeight - Config.chartMarginT - Config.chartMarginB, 0]);

        const svg = d3.select('.chartbox')
            .append('svg')
            .attr('fill', 'none')
            .attr('stroke-linejoin', 'round')
            .attr('stroke-linecap', 'round')
            .attr('class', 'chart')
            .style('width', Config.chartWidth)
            .style('height', Config.chartHeight);

        const color = d3.hsl('#fea');

        // ------x轴------
        const xg = svg
            .append('g')
            .attr('class', 'x axis')
            .attr('transform', `translate(${Config.chartMarginL}, ${Config.chartHeight - Config.chartMarginB})`);
        d3.axisBottom(x)
            .ticks(5)
            .tickSize(0)
            .tickPadding(8)
            .tickFormat(((d) => {
                return XData[d];
            }))(xg);
        xg.selectAll('.tick text').attr('dx', 40);

        // -----y轴------
        const formate = d3.format('.' + d3.precisionFixed(0.1) + 'f');
        const yg = svg
            .append('g')
            .attr('class', 'y axis')
            .attr('transform', `translate(${Config.chartMarginT}, ${Config.chartMarginL})`);

        d3.axisRight(y)
            .ticks(10, formate)
            .tickSizeInner(Config.chartWidth - Config.chartMarginR - Config.chartMarginL)
            .tickPadding(8)(yg);

        // 移除y轴的竖线
        yg.select('.domain').remove();
        // 对y轴tick中的line设置线的样式
        yg.selectAll('.tick:not(:first-of-type) line')
            .attr('stroke-opacity', 0.5)
            .attr('stroke-dasharray', '2,2');
        // 设置y轴的文字的位置
        yg.selectAll('.tick text').attr('x', -2).attr('dy', -4);

        // 绘制柱状矩形
        // let dynamicColor: string = '';
        const rects = svg.append('g')
            .selectAll('rect')
            .data(ChartData)
            .enter()
            .append('rect')
            .style('fill', color.toString())
            .attr('x', (d, i) => Config.chartMarginL + x(i))
            .attr('y', (d) => Config.chartMarginT + y(d.value))
            .attr('height', (d) => Config.chartHeight - Config.chartMarginT - Config.chartMarginB - y(d.value))
            .attr('width', (d) => x(1) - 1);
            // .on('mouseover', function(data) {
            //     if (this.style.fill) { dynamicColor = this.style.fill; }
            //     d3.select(this).style('fill', '#3c763d');
            // })
            // .on('mouseout', function(data) {
            //     d3.select(this).style('fill', dynamicColor);
            // });

        const rectPoint = [];
        ChartData.map((d, i) => {
            rectPoint.push([Config.chartMarginL + x(i) + (x(1) - 1) / 2, Config.chartMarginT + y(d.value)]);
        });
        // 处理交互操作
        const quadtree = d3.quadtree()
            .extent([[Config.chartMarginL, Config.chartMarginT], [Config.chartWidth - Config.chartMarginL, Config.chartHeight - Config.chartMarginB]])
            .addAll(rectPoint);

        const nodes = [];
        quadtree.visit((node, x0, y0, x1, y1) => {
            node.x0 = x0;
            node.y0 = y0;
            node.x1 = x1;
            node.y1 = y1;
            nodes.push(node);
        });

        const tooltip = svg.append('g')
            .attr('transform', 'translate(-100,-100)')
            .attr('class', 'focus');

        tooltip.append('text')
            .attr('class', 'texttitle')
            .attr('y', -20);

        tooltip.append('text')
            .attr('class', 'content')
            .attr('y', -10);

        svg.selectAll('.node')
            .data(nodes)
            .enter()
            .append('rect')
            .attr('class', 'node')
            .attr('x', (d) => d.x0)
            .attr('y', (d) => d.y0)
            .attr('width', (d) => d.y1 - d.y0)
            .attr('height', (d) => d.x1 - d.x0);

        svg.on('mousemove', function() {
            const m = d3.mouse(this);
            const p = quadtree.find(m[0], m[1]);
            rects.style('fill', (d, i) => {
                if ((Config.chartMarginL + x(i) + (x(1) - 1) / 2) === p[0] && (Config.chartMarginT + y(d.value)) === p[1]){
                    tooltip
                        .transition()
                        .duration(200)
                        .ease(d3.easeLinear)
                        .attr('transform', `translate(${Config.chartMarginL + x(i) + (x(1) - 1) / 2}, ${Config.chartMarginT + y(d.value)})`)
                    tooltip
                        .select('text.texttitle')
                        .text('充电方式偏好人群');
                    tooltip
                        .select('text.content')
                        .text(d.key + ': ' + d.value + '人');
                    return color.brighter(0.7).toString();
                } else {
                    return color.toString();
                }
            });
        });
    };

    const generateKey = (item) => {
        let key = '';
        switch(item.split('-')[1]) {
            case '0':
                key = '电充90';
                break;
            case '10':
                key = '电充80';
                break;
            case '20':
                key = '电充70';
                break;
            case '30':
                key = '电充60';
                break;
            case '40':
                key = '电充50';
                break;
            case '50':
                key = '车充50';
                break;
            case '60':
                key = '车充60';
                break;
            case '70':
                key = '车充70';
                break;
            case '80':
                key = '车充80';
                break;
            case '90':
                key = '车充90';
                break;
        }
        return key;
    };

    d3.json('./json/bar.json').then((data) => {
        const d = [];
        const xData = [];
        for (const key in data.list) {
            if (data.list.hasOwnProperty(key)) {
                const element = data.list[key];
                const tmpKey = generateKey(key);
                d.push({
                    key: generateKey(key),
                    value: Number(element),
                });
                xData.push(tmpKey);
            }
        }

        lineChart(config, d, xData);
    });

  },
  methods: {}
};
</script>

<style lang="scss">
#bar {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
  .chart {
        background:rgba(37,39,46,1);
    }

    .chart text {
        fill: white;
        font: 10px sans-serif;
        text-anchor: end;
    }

    .chart .axis text {
        font: 10px sans-serif;
    }

    .chart .axis path, .chart .axis line {
        fill: none;
        stroke: white;
        shape-rendering: crispEdges;
    }

    .node {
        fill: none;
        stroke: none;
        /* stroke: #ccc;
        shape-rendering: crispEdges; */
    }

    .point--selected {
        fill: red;
        fill-opacity: 1;
        stroke: red;
        stroke-width: 5px;
    }

    .chart .texttitle, .chart .content {
        text-anchor: middle;
        text-shadow: 0 1px 0 #fff, 1px 0 0 #fff, 0 -1px 0 #fff, -1px 0 0 #fff;
    }
}
</style>

